import React, {useCallback, useEffect, useState} from "react";
import {Dropdown, Form, InputGroup} from "react-bootstrap";
import Pagination from "../Main/Utilitaire/Pagination";
import Conge from "../../Model/Conge";
import {differenceInCalendarDays} from "date-fns";
import Detail from "./Detail";

const Validation = () => {
    const[congeData,setData]=useState([])
    const [active,setActive]=useState(1)
    const [isModif,setIsModif]=useState(new Date())

    const [dateDemande,setDateDemande]=useState('')
    const [nom,setNom]=useState('')
    const [departement,setDepartement]=useState('')
    const [filteredData, setFilteredData] = useState([]);


    useEffect(()=>{
        Conge.getAllNoValidate().then((resp)=>{
            setData(resp)
        }).catch((e)=>{
            console.log(e.message)
        })
    },[isModif])

    const dure = useCallback( (dateFin,dateDebut,isMatinDebut,isMatinFin) => {
        let diffDays = differenceInCalendarDays(dateFin, dateDebut);
        if (isMatinDebut && isMatinFin){
            diffDays+=0.5;
        }else if(isMatinDebut && !isMatinFin){

            diffDays+=1;
        }else if(!isMatinDebut && !isMatinFin){
            diffDays+=0.5;
        }
        return Math.max(diffDays, 0);
    },[])

    const Valider = useCallback((conge) => {
        Conge.modifConge({ ...conge, statut: "ACCEPTER" }).then(()=>{
            setIsModif(new Date())
        }).catch((e)=>{
            console.log(e)
        })
    },[])
    const refuser = useCallback((conge) => {
        Conge.modifConge({ ...conge, statut: "REFUSER" }).then(()=>{
            setIsModif(new Date())
        }).catch((e)=>{
            console.log(e)
        })
    },[])

    const [congeClicked,setCongeClicked]=useState(null)
    const [show,setShow]=useState(false)

    const clickedConge = useCallback((conge)=> {
        setCongeClicked(conge)
        setShow(true)
    },[])

    useEffect(()=>{
        if (!show){
            setCongeClicked(null)
        }
    },[show])
    const normalizeString = useCallback(str=>{
        return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase();
    },[])
   const filter = () => {
       let fil = congeData;
       if (dateDemande){
           fil = fil.filter(conge =>
               new Date(conge.dateDemande) === new Date(dateDemande)
           );
       }
       if (nom){
           fil = fil.filter(conge =>
               conge.employe.nom.toLowerCase().includes(nom.toLowerCase()) ||
               conge.employe.prenom.toLowerCase().includes(nom.toLowerCase())
           );
       }
       if (departement){
           fil = fil.filter(conge =>
               normalizeString(conge.employe.type.poste).includes(normalizeString(departement))
           );
       }
       setFilteredData(fil)
   }
    useEffect(()=>{
        filter()
    },[congeData,dateDemande, nom, departement,])
    return(
        <>
            <div className="card mb-3">
                <div className="card-body">
                    <h4 className="text-center mb-2">Liste de demandes</h4>
                    <div className="row my-2 p-0">
                        <div className="col-4">
                            <InputGroup className="my-3">
                                <InputGroup.Text id="basic-addon1">Date demande</InputGroup.Text>
                                <Form.Control
                                    placeholder="Date"
                                    aria-label="date"
                                    aria-describedby="dateDemande"
                                    type="date"
                                    value={dateDemande}
                                    onChange={(event) => setDateDemande(event.target.value)}
                                />
                            </InputGroup>
                        </div>
                        <div className="col-4">
                            <InputGroup className="my-3">
                                <InputGroup.Text id="basic-addon1">Nom</InputGroup.Text>
                                <Form.Control
                                    placeholder="nom ou prénom"
                                    aria-label="nom"
                                    aria-describedby="nom"
                                    type="search"
                                    value={nom}
                                    onChange={(event) => setNom(event.target.value)}
                                />
                            </InputGroup>
                        </div>
                        <div className="col-4">
                            <InputGroup className="my-3">
                                <InputGroup.Text id="basic-addon1">Département</InputGroup.Text>
                                <Form.Control
                                    placeholder="poste"
                                    aria-label="Poste"
                                    aria-describedby="Poste"
                                    type="search"
                                    value={departement}
                                    onChange={(event) => setDepartement(event.target.value)}
                                />
                            </InputGroup>
                        </div>
                    </div>
                    <table className="table">
                        <thead>
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Nom</th>
                            <th scope="col">Departement</th>
                            <th scope="col">Du </th>
                            <th scope="col">Au </th>
                            <th scope="col">Durré </th>
                            <th scope="col">Satut</th>
                            <th scope="col">Option</th>
                        </tr>
                        </thead>
                        <tbody>
                        {filteredData.slice((active-1)*10,(active*10)).map((element,index)=>(
                            <tr key={index}>
                                <th scope="row"><pre className="p-0 my-auto mx-0 fs-6">{element.dateDemande}</pre></th>
                                <td>{element.employe.nom} {element.employe.prenom}</td>
                                <td><pre className="p-0 my-auto mx-0 fs-6">{element.employe.type.poste}</pre></td>
                                <td><pre className="p-0 my-auto mx-0 fs-6">{element.dateDebut} {element.matinDebut ? 'Matin' : 'Midi'}</pre></td>
                                <td><pre className="p-0 my-auto mx-0 fs-6">{element.dateFin} {element.matinFin ? 'Matin' : 'Midi'}</pre></td>
                                <td><pre className="p-0 my-auto mx-0 fs-6 fw-bolder text-center text-white rounded-2 bg-danger">{dure(new Date(element.dateFin),new Date(element.dateDebut),element.matinDebut,element.matinFin)}</pre></td>
                                <td><span className={`px-2 py-1 my-auto rounded-2 text-white ${element.statut==="REFUSER" ? 'bg-danger' :element.statut==='ACCEPTER' ? 'bg-success': 'bg-warning' }`}>{element.statut}</span></td>
                                <td>
                                    <Dropdown>
                                        <Dropdown.Toggle
                                            id={`dropdown-${index}`}
                                            as={"span"}
                                            className="px-2 py-1 my-auto border rounded-2 text-muted cursor-pointer"
                                        >
                                            Option
                                        </Dropdown.Toggle>
                                        <Dropdown.Menu>
                                            {element.statut==="ENCOURS" && (
                                                <>
                                                    <Dropdown.Item onClick={()=>Valider(element)} >Valider</Dropdown.Item>
                                                    <Dropdown.Item onClick={()=>refuser(element)}>Refuser</Dropdown.Item>
                                                    <Dropdown.Item onClick={()=>clickedConge(element)}>Détail</Dropdown.Item>
                                                </>
                                            )}
                                        </Dropdown.Menu>
                                    </Dropdown>
                                </td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                    <div className="d-flex justify-content-end w-100 m-0 p-0">
                        <Pagination length={Math.floor((filteredData.length/10))+1} active={active} setActive={setActive}/>
                    </div>
                </div>
            </div>
            {congeClicked && (<Detail conge={congeClicked} show={show} close={()=>setShow(false)} />)}
        </>
    )
}
export default Validation