import React, {useCallback, useMemo, useState} from "react";
import {Modal} from "react-bootstrap";
import {addDays, differenceInCalendarDays} from "date-fns";

const Detail = ({conge, show , close}) => {
    const [dateRetour,setDateRetour]=useState(new Date())
    const format=useCallback((date)=>{
        const year = String(date.getFullYear()).padStart(4, '0');
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${day}/${month}/${year}`
    },[])
    const formatNumberDay = (value, isDebut,isFin) => {
        if (!isDebut && !isFin){
            return Math.floor(value)+1
        }
        return Math.floor(value)
    }
    const dure = useMemo( () => {
        let diffDays = differenceInCalendarDays(conge.dateFin, conge.dateDebut);
        if (conge.matinDebut && conge.matinFin){
            diffDays+=0.5;
        }else if(conge.matinDebut && !conge.matinFin){
            diffDays+=1;
        }else if(!conge.matinDebut && !conge.matinFin){
            diffDays+=0.5;
        }
        const valiny=Math.max(diffDays, 0)
        setDateRetour( ()=>{
            if (valiny===0){
                return "--/--/---- : --"
            }
            return format(addDays(conge.dateDebut, formatNumberDay(valiny, conge.matinDebut,conge.matinFin)))+`${conge.matinFin ? ' : Midi' : ' : Matin'}`
        });
        return valiny;
    },[conge])
    return (
        <>
            <Modal
                show={show}
                onHide={close}
                keyboard={false}
                backdrop="true"
                dialogClassName="modal-dialog-centered modal-90w position-relative d-flex justify-content-center align-items-center"
                contentClassName="d-flex justify-content-center w-100"
                animation
            >
                <Modal.Header closeButton>
                    <Modal.Title className={`${conge.statut==="ENCOURS" ? 'text-warning' : conge.statut=== "REFUSER" ? 'text-danger' : 'text-concept'} fs-5`}>Detail du Congé ({conge.statut})</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="row m-0 p-0">
                        <div className="col-12 mb-3 bg-light border rounded-2  p-2">
                            <h2 className="text-center fs-5 text-capitalize">{conge.employe.nom} {conge.employe.prenom}</h2>
                        </div>
                        <div className="col-12 mb-3 bg-light border rounded-2">
                            <div className="row m-0 p-0 w-100">
                                <div className="col-6 p-2 border-end">
                                    <pre className="p-0 my-auto mx-0 text-center fs-5 mb-1">{conge.dateDebut} {conge.matinDebut ? 'Matin' : 'Midi'}</pre>
                                    <div className="d-flex justify-content-start">
                                        <pre className="my-auto mx-0 fs-6 text-info bg-concept px-2 py-1 rounded">{dateRetour}</pre>
                                    </div>
                                </div>
                                <div className="col-6 p-2">
                                    <pre className="p-0 my-auto mx-0 fs-5 text-center mb-1">{conge.dateFin} {conge.matinFin ? 'Matin' : 'Midi'}</pre>
                                    <div className="d-flex justify-content-end">
                                        <div className="row">
                                            <div className="col-auto text-center border-end bg-danger rounded-2 text-white">
                                                {dure}
                                            </div>
                                            <div className="col-auto">
                                                <pre className="p-0 my-auto mx-0 fs-6 text-info">{dateRetour}</pre>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-12 mb-1 bg-light border rounded-2  p-2">
                            <p className="text-center fs-6 p-0 m-0">{conge.cause!=="" ? conge.cause : 'Cause....'}</p>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
        </>
    )
}
export default Detail