import React, {useCallback, useEffect, useRef, useState} from "react";
import {Button, Col, Form, Row, Spinner, Toast} from "react-bootstrap";
import {useLocation, useNavigate} from 'react-router-dom';
import Allinfo from "../Model/Allinfo";

const InfoLogin = ({setText}) => {
    const { search } = useLocation();
    const navigate=useNavigate()
    const queryParams = new URLSearchParams(search);

    useEffect(()=>{
        if (queryParams.size<19){
            navigate("/inscription/")
            return
        }
        setText("Information de Connexion")
    },[queryParams])
    const [login,setLogin]=useState('')
    const [mdp,setMdp]=useState('')
    const [validation,setValidation]=useState('')
    const [showMdp,setShowMdp]=useState(false)
    const [validated, setValidated] = useState(false);

    const [loading,setLoading]=useState(false)
    const [erreur,setErreur]=useState(false)
    const [erreurText,setErreurText]=useState('')
    const handleSubmit = (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
        }else{
            if (validation!==mdp){
                return
            }else{
                setLoading(true)
                if (image===null){
                    capturePhoto()
                    // handleSubmit()
                }
                const allInfo=new Allinfo(
                    JSON.parse(decodeURIComponent(queryParams.get("type"))),
                    queryParams.get('nom'),
                    queryParams.get('prenom'),
                    new Date(queryParams.get('naissance')),
                    queryParams.get('tel'),
                    queryParams.get('adresse'),
                    queryParams.get('cin'),
                    new Date(queryParams.get('valide')),
                    queryParams.get('nationalite'),
                    JSON.parse(decodeURIComponent(queryParams.get("genre"))),
                    JSON.parse(decodeURIComponent(queryParams.get("statut"))),
                    queryParams.get('enfant'),
                    JSON.parse(decodeURIComponent(queryParams.get("niveau"))),
                    new Date(queryParams.get('entre')),
                    queryParams.get('numCanps'),
                    JSON.parse(decodeURIComponent(queryParams.get("banque"))),
                    queryParams.get('rib'),
                    queryParams.get('contact'),
                    queryParams.get('telContact'),
                    login,
                    mdp,
                    -100,
                    image
                )
                Allinfo.addEmployee(allInfo).then((res)=>{
                    if (res){
                        window.electronStore.set('token', res);
                        navigate("/accueil")
                    }
                }).catch((e)=>{
                    setErreur(true)
                    setErreurText(e.message)
                }).finally(()=>{
                    setLoading(false)
                })
            }
        }

        setValidated(true);
    };
    const [image, setImage] = useState(null);
    const videoRef = useRef(null);
    const canvasRef = useRef(null);
    let steam=null

    const startCamera = () => {
        navigator.mediaDevices.getUserMedia({ video: true })
            .then((Mstream) => {
                steam=Mstream
                videoRef.current.srcObject = Mstream;
                videoRef.current.play();
            })
            .catch((err) => {
                console.error("Erreur d'accès à la caméra: ", err);
            });
    };
    const capturePhoto = () => {
        const canvas = canvasRef.current;
        const video = videoRef.current;
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        const context = canvas.getContext('2d');
        context.drawImage(video, 0, 0, canvas.width, canvas.height);//130*130px
        const imageData = canvas.toDataURL('image/png');
        setImage(imageData);
        stopCamera();
    };
    const captClicked=useCallback(()=>{
        if (image!==null){
            setImage(null)
        }else{
            capturePhoto()
        }
    },[image])
    const stopCamera = () => {
        if (steam) {
            steam.getTracks().forEach(track => track.stop());
        }
    };
    const [showDelete,setShowDelete]=useState(false)
    useEffect(()=>{
        if (image===null){
            startCamera()
            setShowDelete(false)
        }else{
            stopCamera()
            setShowDelete(true)
        }
    },[image])

    const handleFileUpload = (e) => {
        stopCamera();
        const file = e.target.files[0];
        const reader = new FileReader();
        reader.onloadend = () => {
            setImage(reader.result);
        };
        if (file) {
            reader.readAsDataURL(file);
        }
    };
    const fileInputRef = useRef(null);
    const handleDivClick = () => {
        fileInputRef.current.click();
    };
    return(
        <>
            <Form noValidate validated={validated} autoComplete={"off"} onSubmit={handleSubmit}>
                <Row className="mb-1 d-flex justify-content-center">
                    <div className="d-flex position-relative justify-content-center p-2">
                        <input
                            type="file"
                            accept="image/*"
                            ref={fileInputRef}
                            style={{ display: 'none' }}
                            onChange={handleFileUpload}
                        />
                        {image ? (
                            <img
                                src={image}
                                alt="captured or uploaded"
                                className="border-dark-light rounded-circle z-1"
                                width={130} height={130}
                                style={{ objectFit: 'cover' }}
                            />
                        ) : (
                            <>
                                <div className="d-flex justify-content-center">
                                    <video ref={videoRef} className="border-dark-light rounded-circle " width={130} height={130} style={{ objectFit: 'cover', transform: 'scale(1.5)' }} />
                                    <canvas ref={canvasRef} style={{ display: 'none' }} />
                                </div>

                            </>
                        )}
                        <div className={`${showDelete ? 'position-absolute z-3 top-0 bg-opacity-50 ms-7 bg-white width-height-sam rounded-circle d-flex justify-content-center shadow-lg' : 'd-none'}`}>
                            <i className="fa-solid fa-xmark text-danger my-auto cursor-pointer" onClick={()=>setImage(null)} />
                        </div>
                        <div className={`position-absolute z-2 bottom-0 bg-opacity-50  bg-white rounded-pill d-flex justify-content-between shadow-lg`}>
                            <i className="fa-solid fa-image text-info py-2 px-3 cursor-pointer" onClick={handleDivClick}/>
                            <i className="fa fa-camera text-success py-2 px-3 cursor-pointer" onClick={()=>captClicked()}/>
                        </div>
                    </div>
                </Row>
                <Row className="mb-3">
                    <Form.Group className="mb-3" as={Col} md="12" controlId="nom">
                        <Form.Label className=" fw-bold">Email/Nom</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent text-white border-dark-light"
                            placeholder="email"
                            value={login}
                            onChange={(e)=>setLogin(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="mdp">
                        <Form.Label className="fw-bold">Mot de passe</Form.Label>
                        <Form.Control
                            required
                            type={showMdp ? 'text' : 'password'}
                            className="bg-transparent text-white border-dark-light"
                            placeholder="Rakoto"
                            value={mdp}
                            onChange={(e)=>setMdp(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="validation">
                        <Form.Label className="fw-bold">Validation mot de passe</Form.Label>
                        <Form.Control
                            required
                            type={showMdp ? 'text' : 'password'}
                            className="bg-transparent text-white border-dark-light"
                            placeholder="Randria"
                            value={validation}
                            onChange={(e)=>setValidation(e.target.value)}
                            isValid={validation===mdp}
                        />
                    </Form.Group>

                    <Form.Group className="mb-3" as={Col} md="12">
                        <Form.Check
                            type={'checkbox'}
                            id={`default-${'checkbox'}`}
                            label={`Afficher le mot de passe`}
                            value={showMdp}
                            onClick={()=>setShowMdp(!showMdp)}
                        />
                    </Form.Group>
                </Row>
                <Row>
                    <Col md={6}>
                        <button type="button" onClick={()=>{navigate(`/inscription/rh-info?${queryParams.toString()}`)}} className="btn btn-lg bg-aris text-white w-100">Précedent</button>
                    </Col>
                    <Col md={6}>
                        <button type={loading ? 'button' : 'submit'} className="btn btn-lg bg-blue text-white w-100">
                            {loading ? (
                                <Spinner size={"sm"} animation="border" role="status">
                                    <span className="visually-hidden">Loading...</span>
                                </Spinner>
                            ) : ('Terminer')}
                        </button>
                    </Col>
                </Row>
            </Form>
            <div className="position-fixed bottom-0 end-0 width-max p-2">
                <Toast bg={"danger"} show={erreur} onClose={()=>setErreur(false)} autohide delay={5000}>
                    <Toast.Header>
                        <i className="rounded me-2 text-aris icon-30 fas fa-circle-exclamation"/>
                        <strong className="me-auto">Erreur</strong>
                        <small className="text-muted">Maintenant</small>
                    </Toast.Header>
                    <Toast.Body className><strong>{erreurText}</strong></Toast.Body>
                </Toast>
            </div>
        </>
    )
}

export default InfoLogin