import React, {useCallback, useEffect, useState} from "react";
import {Dropdown} from "react-bootstrap";
import EtatEmployer from "../../../Model/EtatEmployer";
import VoirInfo from "./VoirInfo";
import {APIURL} from "../../../Model/APIURL";
import InfoLogin from "../../InfoLogin";

const formatDateToInputValue = (date) => {
    const year = String(date.getFullYear()).padStart(4, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
};

const ValiderSalarie = () => {

    const [nonValidate,setNonValidate]=useState([])
    const [personClicked,setPersonClicked]=useState(null)
    const [show,setShow]=useState(false)
    const[modif,setModif]=useState(true)
    useEffect(()=>{
        EtatEmployer.getEmpNonValidate().then((res)=>{
            console.log(res)
            setNonValidate(res)
        }).catch((er)=>{
            console.log(er.message)
        })
    },[modif])
    const close=useCallback(()=>{
        setShow(false)
        setPersonClicked(null)
    },[])
    useEffect(()=>{
        if (personClicked){
            setShow(true)
        }
    },[personClicked])
    const clickedPerson=useCallback((e)=>{
        setPersonClicked(e)
    },[])

    const [isDeleteClicked,setDeleteClicked]=useState(false)
    const [idEmp,setIdEmp]=useState(-100)

    const validateEmp=useCallback((idEmp)=>{
        EtatEmployer.validate(idEmp).then((res)=>{
            setModif((prev)=>!prev)
        }).catch((e)=>{
            console.log(e)
        })
    },[])
    return (
        <>
            <div>
                <table className="table">
                    <thead>
                        <tr>
                            <th scope="col"/>
                            <th scope="col">#</th>
                            <th scope="col">Nom</th>
                            <th scope="col">Prenom</th>
                            <th scope="col">Poste</th>
                            <th scope="col">Date d'entrée</th>
                            <th scope="col"/>
                        </tr>
                    </thead>
                    <tbody>
                    {nonValidate.map((e)=>(
                        <tr key={e.idEtat}>
                            <td>
                                {e?.emp.employe.profile && (
                                    <img
                                        src={APIURL()+`${e?.emp.employe.profile}`}
                                        alt="captured or uploaded"
                                        className="border-dark-light rounded-circle mt-0 pt-0"
                                        width={40} height={40}
                                        style={{ objectFit: 'cover' }}
                                    />
                                )}
                            </td>
                            <th scope="row">{String(e.emp.employe.idEmp).padStart(4, '0')}</th>
                            <td>{e.emp.employe.nom}</td>
                            <td>{e.emp.employe.prenom}</td>
                            <td>{e.emp.employe.type.poste}</td>
                            <td>{formatDateToInputValue(new Date(e.emp.dateEntre))}</td>
                            <td>
                                <Dropdown>
                                    <Dropdown.Toggle
                                        id={`dropdown-${1}`}
                                        as={"button"}
                                        className="btn btn-sm border"
                                    >
                                        Option
                                    </Dropdown.Toggle>
                                    <Dropdown.Menu>
                                        <Dropdown.Item onClick={()=>{clickedPerson(e)}}
                                        >Voir ses Infos</Dropdown.Item>
                                        <Dropdown.Item onClick={()=>validateEmp(e.emp.employe.idEmp)}>Valider</Dropdown.Item>
                                        <Dropdown.Item
                                            //     onClick={()=>{
                                            //     const queryParams = new URLSearchParams({id: element.emp.employe.idEmp, name: element.emp.employe.nom, firstName: element.emp.employe.prenom});
                                            //     navigate(`/conge/solde?${queryParams.toString()}`)
                                            // }}
                                        >Supprimer</Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </td>
                        </tr>
                    ))}
                    </tbody>
                </table>
            </div>
            {personClicked && (
                <VoirInfo show={show} close={close} person={personClicked}/>
            )}
        </>
    )
}

export default ValiderSalarie