import React, {useCallback, useMemo} from "react";
import {Modal} from "react-bootstrap";
import { differenceInYears } from 'date-fns'
import {APIURL} from "../../../Model/APIURL";

const VoirInfo = ({show, close, person}) => {
    const age=useMemo(()=>{
        if (person && person.emp.employe.naissance) {
            return differenceInYears(new Date(), new Date(person.emp.employe.naissance));
        }
        return 0;
    },[person])
    const formatDateToInputValue = useCallback((date) => {
        const year = String(date.getFullYear()).padStart(4, '0');
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${day}/${month}/${year}`;
    },[])
    return(
        <>
            <Modal
                show={show}
                onHide={close}
                keyboard={false}
                backdrop="static"
                dialogClassName="modal-dialog-centered modal-90w position-relative d-flex justify-content-center align-items-center"
                contentClassName="d-flex justify-content-center w-100"
            >
                <Modal.Header closeButton>
                    <Modal.Title className="">Info sur un employé</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {person?.emp.employe.profile && (
                        <div className="d-flex justify-content-center mt-0 pt-0">
                            <img
                                src={APIURL()+`${person?.emp.employe.profile}`}
                                alt="captured or uploaded"
                                className="border-dark-light rounded-circle mt-0 pt-0"
                                width={130} height={130}
                                style={{ objectFit: 'cover' }}
                            />
                        </div>
                    )}
                    <div className="d-flex justify-content-start mb-3">
                        <h6 className="small text-uppercase fw-bolder">Info Personelle</h6>
                    </div>
                    <div className="row m-0 p-0">
                        <div className="col-4 mb-2 border-end">
                            <pre className="m-0 p-0">Nom : <span className="text-center fs-5 text-uppercase">{person?.emp.employe.nom}</span></pre>
                        </div>
                        <div className="col-4 mb-2 border-end">
                            <pre className="m-0 p-0">Prenom : <span className="text-center fs-5 text-capitalize"> {person?.emp.employe.prenom}</span></pre>
                        </div>
                        <div className="col-4 mb-2 border-end">
                            <pre className="m-0 p-0">Tel : <span className="text-center fs-5 text-capitalize">{person?.emp.employe.tel}</span></pre>
                        </div>
                        <hr/>
                        <div className="col-4 mb-2 border-end">
                            <pre className="m-0 p-0">Nationalité : <span className="text-center fs-5">{person.emp.employe.nationalite}</span></pre>
                        </div>
                        <div className="col-4 mb-2 border-end">
                            <pre className="m-0 p-0">CIN : <span className="text-center fs-5">{person.emp.employe.cin}</span></pre>
                        </div>
                        <div className="col-4 mb-2 border-end">
                            <pre className="m-0 p-0">Adresse : <span className="text-center fs-5">{person.emp.employe.adresse}</span></pre>
                        </div>
                        <hr/>
                        <div className="col-3 mb-2 border-end">
                            <pre className="m-0 p-0">Genre : <span className="text-center fs-5">{person.emp.employe.genre.genre}</span></pre>
                        </div>
                        <div className="col-3 mb-2 border-end">
                            <pre className="m-0 p-0">Statut Mat : <span className="text-center fs-5">{person.emp.employe.statut.value}</span></pre>
                        </div>
                        <div className="col-3 mb-2 border-end">
                            <pre className="m-0 p-0">Nb enfant : <span className="text-center fs-5">{person.emp.employe.enfant}</span></pre>
                        </div>
                        <div className="col-3 mb-2 border-end">
                            <pre className="m-0 p-0">Age : <span className="text-center fs-5">{age}</span></pre>
                        </div>
                        <hr/>
                        <div className="d-flex justify-content-start mb-3">
                            <h6 className="small text-uppercase fw-bolder">Info RH</h6>
                        </div>
                        <div className="row m-0 p-0 w-100">
                            <div className="col-4 mb-2 border-end">
                                <pre className="m-0 p-0">Date d'entrée : <span className="text-center fs-5">{formatDateToInputValue(new Date(person.emp.dateEntre))}</span></pre>
                            </div>
                            <div className="col-4 mb-2 border-end">
                                <pre className="m-0 p-0">Etude : <span className="text-center fs-5">{person.emp.employe.niveau.value}</span></pre>
                            </div>
                            <div className="col-4 mb-2 border-end">
                                <pre className="m-0 p-0">Poste : <span className="text-center fs-5">{person.emp.employe.type.poste}</span></pre>
                            </div>
                            <hr/>
                            <div className="col-4 mb-2 border-end">
                                <pre className="m-0 p-0">CNAPS : <span className="text-center fs-5">{person.emp.numCnaps==='' ? 'null' : person.emp.numCnaps}</span></pre>
                            </div>
                            <div className="col-4 mb-2 border-end">
                                <pre className="m-0 p-0">Banque : <span className="text-center fs-5">{person.emp.rib==='' ? 'null' : person.emp.banque.value}</span></pre>
                            </div>
                            <div className="col-4 mb-2 border-end">
                                <pre className="m-0 p-0">RIB : <span className="text-center fs-5">{person.emp.rib==='' ? 'null' : person.emp.rib}</span></pre>
                            </div>
                            <hr/>
                        </div>
                        <div className="d-flex justify-content-start mb-3">
                            <h6 className="small text-uppercase fw-bolder">Contact Rapide</h6>
                        </div>
                        <div className="row m-0 p-0 w-100">
                            <div className="col-6 mb-2 border-end">
                                <pre className="m-0 p-0">Nom du contact : <span className="text-center fs-5">{person.emp.contact}</span></pre>
                            </div>
                            <div className="col-6 mb-2 border-end">
                                <pre className="m-0 p-0">Numeros : <span className="text-center fs-5">{person.emp.tel}</span></pre>
                            </div>
                            <hr/>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
        </>
    )
}
export default VoirInfo