import {APIURL} from "./APIURL";

export default class EtatEmployer {
    static async getEmployee() {
        const response = await fetch(APIURL() + "employe/all" , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json'
            }
        });

        if (response.ok) {
            return await response.json();
        }else{
            console.log("statut : "+response.status)
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }
    static async getEmpNonValidate() {
        const response = await fetch(APIURL() + "employe/nonValider" , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json'
            }
        });

        if (response.ok) {
            return await response.json();
        }else{
            console.log("statut : "+response.status)
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }
    static async supprimerEmp() {
        const response = await fetch(APIURL() + "employe/" , {
            method: 'delete',
            headers: {
                "Content-Type": 'application/json'
            }
        });

        if (response.ok) {
            return true;
        }else{
            console.log("statut : "+response.status)
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }

    static async validate(idEmp) {
        const response = await fetch(APIURL() + "employe/"+idEmp , {
            method: 'put',
            headers: {
                "Content-Type": 'application/json'
            },
        });

        if (response.status===400){
            throw new Error(await response.text())
        } else if (response.ok) {
            return true
        }else{
            console.log("statut : "+response.status)
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }
}